
























































































































































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
export default {
	mixins:[mixin],
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			starnum:0,
			is_show:false,
			current_sign:0,
			current_info:{},
			current_imgs:[],
			bid:0,
			signshow:false,
			signerror:false,
			finisherror:false,
			finishcode:false,
			ewmcode:'',
			rules_photo:[],
			codeid:0,
			code:2
		};
	},
	methods:{
		async init(){
			let bid = uni.getStorageSync("bid")
			this.bid = bid
			Utils.loading('正在加载')
			let missionDetail = await api.getMission({
				uid:this.userInfo.uid,
				bid:bid
			})
			if(missionDetail.status == 1){
				Utils.loaded()
				let data = missionDetail.data
				this.current_sign = data.current_sign || 0
				this.current_info = data.current_info
				this.current_imgs = this.current_info.detail_photo ? JSON.parse(this.current_info.detail_photo) : []
				this.starnum = this.current_info.sign_time
				this.rules_photo = this.current_info.rules_photo ? JSON.parse(this.current_info.rules_photo) : []
			}
		},
		showModel(){
			let isshow = this.is_show
			if(isshow){
				this.is_show = false
			}else{
				this.is_show = true
			}
		},
		showSignModel(){
			let isshow = this.signshow
			if(isshow){
				this.signshow = false
			}else{
				this.signshow = true
			}
		},
		showerrorModel(){
			let isshow = this.signerror
			if(isshow){
				this.signerror = false
			}else{
				this.signerror = true
			}
		},
		showfinishModel(){
			let isshow = this.finisherror
			if(isshow){
				this.finisherror = false
			}else{
				this.finisherror = true
			}
		},
		showfinishCodeModel(){
			let isshow = this.finishcode
			if(isshow){
				this.finishcode = false
			}else{
				this.finishcode = true
			}
		},
		// 签到
		async actionSign(){
			let actionResult = await api.actionSign({
				uid:this.userInfo.uid,
				bid:this.bid
			})
			if(actionResult.status == 1){
				this.current_sign = actionResult.data
				this.signshow = true
			}else{
				this.signerror = true;
			}
		},
		// 提交任务
		async finishMission(){
			let _this = this
			// 判断用户是否签到已满
			let signall = _this.starnum
			let signnum = _this.current_sign
			if(signnum == signall){
				let response = await api.finishMission({
					uid:_this.userInfo.uid,
					bid:_this.bid
				})
				if(response.status == 1){
					// this.init()
					_this.ewmcode = response.data
					_this.finishcode = true
					// 定时循环判断用户是否被扫成功 成功刷新
					let time = setInterval(() => {
						_this.getCode()
						if(_this.code == 1){
							_this.changeCode()
							clearInterval(time)
						}
					},1000)
				}
			}else{
				_this.finisherror = true
			}
		},
		
		// 动态获取状态值
		async getCode(){
			let getStatus = await api.getCodeStatus({
				uid:this.userInfo.uid,
				bid:this.bid
			})
			this.code = getStatus.data.code_status
			this.codeid = getStatus.data.id
		},
		
		// 改变状态值
		async changeCode(){
			let changeResult = await api.changeCode({
				current_id:this.codeid
			})
			if(changeResult.status == 1){
				Utils.toast('完成任务！')
				setTimeout(() => {
					this.finishcode = false
					this.init()
				},1000)
			}
		}
		
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	}
}
